// ===== Ê®°Êì¨Âô®ÊéßÂà∂ =====

/**
 * ÈñãÂßãÊ®°Êì¨
 */
function startEmulation(): void {
  if (animationId !== null) {
    cancelAnimationFrame(animationId);
  }

  isRunning = true;

  // NES NTSC ÂπÄÁéáÔºö60.0988 fps
  const TARGET_FRAME_TIME = 1000 / 60.0988;
  let lastFrameTime = performance.now();
  let accumulator = 0;

  const frameLoop = (currentTime: number): void => {
    if (!nes || !ctx || !imageData || !isRunning) return;

    const deltaTime = currentTime - lastFrameTime;
    lastFrameTime = currentTime;
    
    accumulator += deltaTime;
    
    if (accumulator > TARGET_FRAME_TIME * 3) {
      accumulator = TARGET_FRAME_TIME;
    }

    while (accumulator >= TARGET_FRAME_TIME) {
      nes.frame();
      accumulator -= TARGET_FRAME_TIME;
    }

    renderFrame();
    animationId = requestAnimationFrame(frameLoop);
  };

  animationId = requestAnimationFrame(frameLoop);
}

/**
 * ÂÅúÊ≠¢Ê®°Êì¨
 */
function stopEmulation(): void {
  isRunning = false;
  if (animationId !== null) {
    cancelAnimationFrame(animationId);
    animationId = null;
  }
}

/**
 * Ê∏≤Êüì‰∏ÄÂπÄÂà∞Áï´Â∏É
 */
function renderFrame(): void {
  if (!nes || !ctx || !imageData) return;

  const frameBuffer = nes.getFrameBuffer();
  const data = imageData.data;

  for (let i = 0; i < 256 * 240; i++) {
    const pixel = frameBuffer[i];
    const offset = i * 4;
    data[offset + 0] = (pixel >> 16) & 0xFF;
    data[offset + 1] = (pixel >> 8) & 0xFF;
    data[offset + 2] = pixel & 0xFF;
    data[offset + 3] = 255;
  }

  ctx.putImageData(imageData, 0, 0);
}

// ===== Èü≥È†ªÁ≥ªÁµ± =====

/**
 * ÂàùÂßãÂåñÈü≥È†ªÁ≥ªÁµ±
 */
async function initAudio(): Promise<void> {
  try {
    audioContext = new AudioContext({ sampleRate: 44100 });
    
    if (nes) {
      nes.setAudioSampleRate(audioContext.sampleRate);
    }
    
    const scriptProcessor = audioContext.createScriptProcessor(AUDIO_BUFFER_SIZE, 0, 1);
    
    scriptProcessor.onaudioprocess = (e) => {
      const output = e.outputBuffer.getChannelData(0);
      if (nes && isRunning) {
        const samplesRead = nes.readAudioSamples(output);
        if (samplesRead === 0) {
          output.fill(0);
        }
      } else {
        output.fill(0);
      }
    };
    
    scriptProcessor.connect(audioContext.destination);
    console.log('Èü≥È†ªÁ≥ªÁµ±Â∑≤ÂàùÂßãÂåñÔºåÂèñÊ®£Áéá:', audioContext.sampleRate);
  } catch (e) {
    console.error('Èü≥È†ªÂàùÂßãÂåñÂ§±Êïó:', e);
  }
}

/**
 * ÊÅ¢Âæ©Èü≥È†ª
 */
function resumeAudio(): void {
  if (audioContext && audioContext.state === 'suspended') {
    audioContext.resume();
  }
}

// ===== Â≠òÊ™îÁ≥ªÁµ± =====

const SAVE_STATE_PREFIX = 'nes_savestate_';

/**
 * È°ØÁ§∫ÊèêÁ§∫Ë®äÊÅØ
 */
function showToast(message: string): void {
  // ÁßªÈô§ËàäÁöÑ toast
  const existingToast = document.querySelector('.toast-message');
  if (existingToast) {
    existingToast.remove();
  }
  
  // Âª∫Á´ãÊñ∞ÁöÑ toast
  const toast = document.createElement('div');
  toast.className = 'toast-message';
  toast.textContent = message;
  toast.style.cssText = `
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 15px 30px;
    border-radius: 10px;
    font-size: 16px;
    font-weight: bold;
    z-index: 10000;
    animation: toastFade 1.5s ease-out forwards;
  `;
  
  // Ê∑ªÂä†ÂãïÁï´Ê®£Âºè
  if (!document.querySelector('#toast-style')) {
    const style = document.createElement('style');
    style.id = 'toast-style';
    style.textContent = `
      @keyframes toastFade {
        0% { opacity: 1; transform: translate(-50%, -50%) scale(1); }
        70% { opacity: 1; transform: translate(-50%, -50%) scale(1); }
        100% { opacity: 0; transform: translate(-50%, -50%) scale(0.8); }
      }
    `;
    document.head.appendChild(style);
  }
  
  document.body.appendChild(toast);
  
  // Ëá™ÂãïÁßªÈô§
  setTimeout(() => toast.remove(), 1500);
}

function saveState(slot: number = 0): boolean {
  if (!nes) return false;
  
  try {
    const saveData = nes.exportSaveState();
    const key = `${SAVE_STATE_PREFIX}${slot}`;
    localStorage.setItem(key, saveData);
    console.log(`Â≠òÊ™îÊàêÂäü (Slot ${slot})`);
    return true;
  } catch (e) {
    console.error('Â≠òÊ™îÂ§±Êïó:', e);
    return false;
  }
}

function loadState(slot: number = 0): boolean {
  if (!nes) return false;
  
  try {
    const key = `${SAVE_STATE_PREFIX}${slot}`;
    const saveData = localStorage.getItem(key);
    
    if (!saveData) {
      console.log(`Slot ${slot} Ê≤íÊúâÂ≠òÊ™î`);
      return false;
    }
    
    const success = nes.importSaveState(saveData);
    if (success) {
      console.log(`ËÆÄÂèñÂ≠òÊ™îÊàêÂäü (Slot ${slot})`);
    }
    return success;
  } catch (e) {
    console.error('ËÆÄÂèñÂ≠òÊ™îÂ§±Êïó:', e);
    return false;
  }
}

function exportSaveToFile(): void {
  if (!nes) return;
  
  const saveData = nes.exportSaveState();
  const blob = new Blob([saveData], { type: 'application/json' });
  const url = URL.createObjectURL(blob);
  
  const a = document.createElement('a');
  a.href = url;
  a.download = `nes_savestate_${Date.now()}.json`;
  a.click();
  
  URL.revokeObjectURL(url);
}

// ===== ÈçµÁõ§Âø´Êç∑Èçµ =====

function setupKeyboardShortcuts(): void {
  document.addEventListener('keydown', (e) => {
    if (e.key === 'F5') {
      e.preventDefault();
      saveState(0);
    }
    if (e.key === 'F7') {
      e.preventDefault();
      loadState(0);
    }
    if (e.key >= 'F1' && e.key <= 'F4' && e.shiftKey) {
      e.preventDefault();
      const slot = parseInt(e.key[1]);
      saveState(slot);
    }
    if (e.key >= '1' && e.key <= '4' && e.ctrlKey) {
      e.preventDefault();
      const slot = parseInt(e.key);
      loadState(slot);
    }
    // ESC ÈçµËøîÂõûÈÅ∏ÊìáÁï´Èù¢
    if (e.key === 'Escape') {
      showRomSelector();
    }
  });
}

// ===== ÂÖ®ÂüüÂåØÂá∫ =====

declare global {
  interface Window {
    nes: Nes | null;
    startEmulation: () => void;
    stopEmulation: () => void;
    saveState: (slot?: number) => boolean;
    loadState: (slot?: number) => boolean;
    exportSaveToFile: () => void;
    showRomSelector: () => void;
  }
}

window.nes = null;
window.startEmulation = startEmulation;
window.stopEmulation = stopEmulation;
window.saveState = saveState;
window.loadState = loadState;
window.exportSaveToFile = exportSaveToFile;
window.showRomSelector = showRomSelector;

// ===== ÂïüÂãï =====

document.addEventListener('DOMContentLoaded', async () => {
  init();
  await initAudio();
  setupKeyboardShortcuts();
  window.nes = nes;
  
  // Áî®Êà∂‰∫§‰∫íÂæåÊÅ¢Âæ©Èü≥È†ª
  document.addEventListener('click', resumeAudio, { once: true });
  document.addEventListener('keydown', resumeAudio, { once: true });
  document.addEventListener('touchstart', resumeAudio, { once: true });
});
